<template>
	<div>
		<h1 class="text-center">Registrar nuevo Libro</h1>
		<v-form v-model="valid" ref="form">
			<v-container>
				<v-row>
					<v-col cols="12" md="12">
						<v-text-field
							v-model="form.title"
							:rules="requireRules"
							label="Nombre Libro"
							required
						></v-text-field>
					</v-col>
					<v-col cols="12" md="12">
						<v-text-field
							v-model="form.author"
							:rules="requireRules"
							label="Autor"
							required
						></v-text-field>
					</v-col>
					<v-col cols="12" md="12">
						<v-text-field
							v-model="form.image"
							:rules="requireRules"
							label="Src imagen"
							required
						></v-text-field>
					</v-col>
					<v-col cols="12" md="12">
						<v-select :items="types" label="Tipo" v-model="form.type" :rules="requireRules"></v-select>
					</v-col>
                    <v-col>
                        <v-btn color="success" @click="createBook">Registrar</v-btn>
                        <v-btn @click="resetForm">Limpiar Formulario</v-btn>
                    </v-col>
				</v-row>
			</v-container>
		</v-form>
	</div>
</template>

<script>
import {db} from '@/firebase/firebase.js'
import {collection, addDoc} from 'firebase/firestore'
export default {
	name: 'Register-view',
	// props: {},
	data: function () {
		return {
			valid: true,
			form: {
				title: '',
				author: '',
				type: '',
				image: '',
			},
            types:['Ciencia ficción','novela'],

			requireRules: [(v) => !!v || 'Campo es requerido'],
		}
	},
	// computed: {},
	methods: {
        async createBook(){
            this.$refs.form.validate() //valida los campos
            if(!this.valid) return //si no es valido return y salimos

            try{
                let response = await addDoc(collection(db,'books'),this.form)
                console.log(response)
                this.$router.push('/')
            }
            catch(error){
                console.log(error)
            }
        },
        resetForm(){
            this.$refs.form.reset()
        }
    }
	// watch: {},
	// components: {},
	// mixins: [],
	// filters: {},
	// -- Lifecycle Methods
	// -- End Lifecycle Methods
}
</script>

<style scoped></style>
