<template>
	<div>
		<h1 class="text-center">Editar Libro</h1>
		<v-form v-model="valid" ref="form">
			<v-container>
				<v-row>
					<v-col cols="12" md="12">
						<v-switch
							v-model="allowEdit"
							label="Editar Libro"
							color="infor"
							value="info"
							hide-details
						></v-switch>
					</v-col>
					<v-col cols="12" md="12">
						<v-text-field
							v-model="form.title"
							:rules="requireRules"
							label="Nombre Libro"
							required
							:disabled="!allowEdit"
						></v-text-field>
					</v-col>
					<v-col cols="12" md="12">
						<v-text-field
							v-model="form.author"
							:rules="requireRules"
							label="Autor"
							required
							:disabled="!allowEdit"
						></v-text-field>
					</v-col>
					<v-col cols="12" md="12">
						<v-text-field
							v-model="form.image"
							:rules="requireRules"
							label="Src imagen"
							required
							:disabled="!allowEdit"
						></v-text-field>
					</v-col>
					<v-col cols="12" md="12">
						<v-select
							:items="types"
							label="Tipo"
							v-model="form.type"
							:rules="requireRules"
							:disabled="!allowEdit"
						></v-select>
					</v-col>
					<v-col>
						<v-btn color="success" @click="editBook" :disabled="!allowEdit">Editar</v-btn>
						<v-btn color="red" @click="dialog=true">Eliminar</v-btn>
						<v-btn @click="resetForm">Limpiar Formulario</v-btn>
					</v-col>
				</v-row>
			</v-container>
		</v-form>
		<v-dialog v-model="dialog" width="500">
			
			<v-card>
				<v-card-title class="text-h5 grey lighten-2">
					¿Deseas Eliminar el libro?
				</v-card-title>

				<v-divider></v-divider>

				<v-card-actions>
                    <v-btn color="red" text @click="deleteBook" >Eliminar</v-btn>
					<v-spacer></v-spacer>
					<v-btn color="primary" text @click="dialog = false"> Cerrar</v-btn>
				</v-card-actions>
			</v-card>
		</v-dialog>
	</div>
</template>

<script>
import { db } from '@/firebase/firebase.js'
import { doc, getDoc, updateDoc , deleteDoc} from 'firebase/firestore'
export default {
	name: 'book-view',
	props: ['idBook'],
	data: function () {
		return {
			valid: true,
			form: {
				title: '',
				author: '',
				type: '',
				image: '',
			},
			types: ['Ciencia ficción', 'novela'],
			allowEdit: false,
			requireRules: [(v) => !!v || 'Campo es requerido'],
            dialog:false
		}
	},
	// computed: {},
	methods: {
		async fetchBookById() {
			const docRef = doc(db, 'books', this.idBook)
			let response = await getDoc(docRef)
			console.log(response)
			this.form = response.data()
		},
		async editBook() {
			this.$refs.form.validate() //valida los campos
			if (!this.valid) return //si no es valido return y salimos

			try {
				let book = this.form
				book.id = this.idBook
				await updateDoc(doc(db, 'books', book.id), book)
				this.$router.push('/')
			} catch (error) {
				console.log(error)
			}
		},
        async deleteBook(){
            try{
                await deleteDoc(doc(db,'books',this.idBook))
                this.$router.push('/')
            }catch(error){
                console.log(error)
            }
        },
		resetForm() {
			this.$refs.form.reset()
		},
	},
	// watch: {},
	// components: {},
	// mixins: [],
	// filters: {},
	// -- Lifecycle Methods
	created() {
		this.fetchBookById()
	},
	// -- End Lifecycle Methods
}
</script>

<style scoped></style>
